
<template>
  <div class="metadata-management-field-relation clearfix">
    <div
      class="relation-operation"
    >
      <gs-search
        v-model="searchFieldName"
        placeholder="..."
        @search="search"
      />
      <!-- <i
        class="operation-i iconfont icon-dayin"
      /> -->
      <i
        class="operation-i iconfont icon-quanping1"
        @click="showFullScreen"
      />
    </div>
    <div
      class="svg-wrap"
    >
      <div
        class="relative svg-content"
      >
        <field-relation-chart
          id="fieldRelationChart"
          ref="fieldRelationChart"
          :style="isFullScreenDisplay ? 'height:100vh;width:100vw;position:fixed;top:0;left:0;background:black;z-index:90;' : ''"
          :current-selected="currentSelected"
          :relation-filter="relationFilter"
          :guid="guid"
          @selectChange="changeSelectItem"
        />

      </div>
    </div>
    <div
      class="relation-ship-detail pull-right"
    >
      <div
        v-loading="isDetailLoading"
        class="relative"
      >
        <div class="text-center tab-relation-filter">
          <el-radio-group v-model="tabPosition">
            <el-radio-button label="relationshipProp"></el-radio-button>
            <el-radio-button label="tableDetail"></el-radio-button>
          </el-radio-group>
          <gs-button
            :disabled="viewRelationship"
            type="text-primary"
            class="relationship-btn"
            @click="loadSelectRelationship"
          ></gs-button>
        </div>
        <div
          v-if="tabPosition === 'relationshipProp'"
          class="relationship-prop"
        >
          <div class="relationship-item">
            <h4></h4>
            <span></span>
            <el-radio
              v-model="relationFilter.direction"
              label="BOTH"
            ></el-radio>
            <el-radio
              v-model="relationFilter.direction"
              label="INPUT"
            ></el-radio>
            <el-radio
              v-model="relationFilter.direction"
              label="OUTPUT"
            ></el-radio>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <number-slider
              :limit-number="relationFilter.limitNumber"
              v-model="relationFilter.selectedDepth"
              @change="selectedDepthChange"
            />
          </div>
        </div>
        <div v-else>
          <div class="relationship-item">
            <h4></h4>
            <span>{{ detailInfo.displayText }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <i
              class="iconfont icon-biaoge"
            />
            <span>{{ detailInfo.tableName }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <i
              class="iconfont icon-yuanshuju"
            />
            <span>{{ detailInfo.dbName }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <span>{{ detailInfo.directUpStreamNum }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <span>{{ detailInfo.directDownStreamNum }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <span>{{ detailInfo.upStreamLevelNum }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <span>{{ detailInfo.downStreamLevelNum }}</span>
          </div>
          <div class="relationship-item">
            <h4></h4>
            <span>{{ detailInfo.updateTime }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import './fieldRelation.scss';
import * as relationshipService from 'src/service/metadata-management/relationship';
import FieldRelationChart from './fieldRelationChart.vue';
import NumberSlider from 'src/components/number-slider';
export default {
  components: {
    FieldRelationChart,
    NumberSlider
  },
  data() {
    return {
      searchFieldName: '',
      tabPosition: 'relationshipProp',
      isDetailLoading: false,
      isFullScreenDisplay: false,
      detailInfo: {
        'tableName': '',
        'dbName': '',
        'displayText': '',
        'directUpStreamNum': '0',
        'directDownStreamNum': '0',
        'upStreamLevelNum': '0',
        'downStreamLevelNum': '0',
        'updateTime': ''
      },
      data: [],
      relationFilter: {
        direction: 'BOTH',
        selectedDepth: 2,
        limitNumber: 6
      },
      currentSelected: {
        // table Id
        tableGuid: this.$route.params.id,
        // Id
        columnGuid: ''
      }
    };
  },
  computed: {
    viewRelationship() {
      return this.$route.params.id === this.currentSelected.tableGuid;
    },
    guid() {
      return this.$route.params.id;
    }
  },
  watch: {
    'relationFilter.direction'(value) {
      this.$refs['fieldRelationChart'].getFieldLineage();
    }
  },
  mounted() {
    let _this = this;
    document.addEventListener('fullscreenchange', function (e) {
      _this.isFullScreenDisplay = !_this.isFullScreenDisplay;
    });
    document.addEventListener('mozfullscreenchange', function (e) {
      _this.isFullScreenDisplay = !_this.isFullScreenDisplay;
    });
    document.addEventListener('webkitfullscreenchange', function (e) {
      _this.isFullScreenDisplay = !_this.isFullScreenDisplay;
    });
    document.addEventListener('msfullscreenchange', function (e) {
      _this.isFullScreenDisplay = !_this.isFullScreenDisplay;
    });
  },
  methods: {
    /**
     * 
     */
    showFullScreen(e) {
      // this.$refs['fullscreen'].toggle();
      function FullScreen(el) {
        var isFullscreen = document.fullScreen || document.mozFullScreen || document.webkitIsFullScreen;
        if (!isFullscreen) {
          (el.requestFullscreen && el.requestFullscreen()) ||
          (el.mozRequestFullScreen && el.mozRequestFullScreen()) ||
          (el.webkitRequestFullscreen && el.webkitRequestFullscreen()) || (el.msRequestFullscreen && el.msRequestFullscreen());
        } else {
          if (document.exitFullscreen) {
            document.exitFullscreen();
          } else {
            if (document.mozCancelFullScreen) {
              document.mozCancelFullScreen();
            } else {
              if (document.webkitExitFullscreen) {
                document.webkitExitFullscreen();
              }
            }
          }
        }
      }
      var el = document.body;
      FullScreen(el);
      setTimeout(() => {
        this.$refs['fieldRelationChart'].refresh();
      }, 100);
    },
    /**
     * 
     */
    fullscreenChange() {
      // this.$refs['fieldRelationChart'].refresh();
    },
    /**
     * 
     */
    changeSelectItem(tableGuid, columnGuid) {
      this.isDetailLoading = false;
      // tableGuid  columnGuid 
      if (!tableGuid && !columnGuid) {
        this.resetDetail();
        return;
      }
      this.currentSelected.tableGuid = tableGuid;
      this.currentSelected.columnGuid = columnGuid;
      this.getColumnLineageInfo();
    },
    /**
     * 
     */
    loadSelectRelationship() {
      this.$router.replace({
        path: `/main/metadata/detail/${this.currentSelected.tableGuid}`,
        query: {
          module: 'relationship',
          showRelationship: 'fieldRelation'
        }
      }, () => {
        this.resetDetail();
        this.$refs['fieldRelationChart'].getFieldLineage();
        this.$emit('refreshTableInfo');
      });
    },
    /**
     * 
     */
    resetDetail() {
      this.currentSelected = {
        // table Id
        tableGuid: this.$route.params.id,
        // Id
        columnGuid: ''
      };
      this.detailInfo = {
        'tableName': '',
        'dbName': '',
        'displayText': '',
        'directUpStreamNum': '',
        'directDownStreamNum': '',
        'upStreamLevelNum': '',
        'downStreamLevelNum': '',
        'updateTime': ''
      };
    },
    /**
     * 
     */
    getColumnLineageInfo() {
      this.isDetailLoading = true;
      const id = this.currentSelected.columnGuid;
      console.log('====================================');
      console.log(this.$route);
      console.log('====================================');
      const tableId = this.$route.params.id;
      relationshipService.getColumnLineageInfo(this.currentSelected.columnGuid, tableId).then((res) => {
        // 
        if (id !== this.currentSelected.columnGuid) {
          return;
        }
        this.isDetailLoading = false;
        this.detailInfo = res;
      }).catch((res) => {
        this.isDetailLoading = false;
      });
    },
    /**
     * 
     */
    selectedDepthChange(value) {
      this.$refs['fieldRelationChart'].getFieldLineage();
    },
    /**
     * 
     */
    search() {
      // 
      this.changeSelectItem();
      this.$refs['fieldRelationChart'].search(this.searchFieldName);
    }
  }
};
</script>



