<template>
  <div
    v-loading="loading"
    class="template-tree"
  >
    <gs-tree
      v-if="!isEmpty"
      ref="tree"
      :node-key="nodeKey"
      :default-expanded-keys="defaultExpandedKeys"
      :render-content="renderTreeNode"
      v-bind="treeProps"
      default-all-expand
      @node-toggle="handleNodeToggle"
    />
  </div>
</template>

<script>
import './template-tree.scss';
import * as service from 'src/service/metadata-management/data-quality';
export default {
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    treeProps: {
      type: Object,
      default() {
        return {};
      }
    },
    nodeKey: {
      type: String,
      default: ''
    },
    defaultExpandedKeys: {
      type: Array,
      default() {
        return [];
      }
    },
    active: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
    };
  },

  computed: {
    isEmpty() {
      return this.loading || this.treeProps.data.length === 0;
    }
  },

  methods: {
    renderTreeNode(h, {node, data}) {
      let {
        treeProps,
        active
      } = this;
      const {
        label,
        value
      } = treeProps.props;
      let itemClass = 'template-tree-node-label';
      let dotClass = 'alert';
      let alertLabel = '0';
      let iconClass = '';
      itemClass += active[value] === data[value] ? ' is-active' : '';
      itemClass += data.enableClick === false ? ' is-disabled' : '';
      if (data.redAlerts > 0) {
        alertLabel = data.redAlerts;
        if (data.alert) dotClass = 'alert red';
      }
      if (data.redAlerts === 0 && data.orangeAlerts > 0) {
        alertLabel = data.orangeAlerts;
        if (data.alert) dotClass = 'alert orange';
      }
      if (data.type === 1) {
        iconClass = 'icon template';
      } else if (data.type === 2) {
        iconClass = 'icon newreport';
      } else if (data.type === 3) {
        iconClass = 'icon report';
      }
      return (
        <div class="template-tree-node-content">
          {(
            <span
              title={data[label]}
              class={itemClass}
              on-click={this.handleClick.bind(this, data)}
            >
              <i class={iconClass}></i>
              {data.isLoading
                ? (<i class="side-tree-node-icon gs-icon-loading"></i>)
                : null
              }{data[label]}
            </span>
          )}
          <div>
            {(data.redAlerts > 0 || data.orangeAlerts > 0)
              ? (<span
                title={data.alert ? '' : ''}
                class={dotClass}
                on-click={this.handleAlertClick.bind(this, data)}
              >{alertLabel}</span>) : null
            }
            { data.type === 1
              ? <span
                title=""
                class="file-add"
                on-click={this.copyTemplate.bind(this, data)}></span>
              : null}
          </div>
        </div>
      );
    },

    copyTemplate(data) {
      this.$emit('copyTemplate', data.buildType, data.templateId);
    },

    handleNodeToggle(node, isToggle) {
      this.$emit('node-toggle', node.data, isToggle);
    },

    handleClick(data) {
      if (data.enableClick === false) {
        return;
      }
      this.$emit('node-click', data);
    },

    handleAlertClick(data) {
      const alert = data.alert === 0 ? 1 : 0;
      this.$Modal.confirm({
        title: '',
        content: `${data.alert === 0 ? '' : ''}`,
        onOk: () => {
          service.changeAlertStatus(data.reportId, alert).then(res => {
            this.$Message.success(`${data.alert === 0 ? '' : ''}`, 1.5);
            this.$emit('alert-click');
          });
        }
      });
      this.$emit('node-click', data);
      // this.$emit('checkReportDetail');
    }
  }
};
</script>




