<template>
  <div
    v-loading="isLoading"
    class="metadata-management-table-info relative"
  >
    <div class="operation">
      <gs-button
        v-if="!tableInfo.virtualTable && tableInfo.status !== 'DELETED' && tableInfo.edit"
        :style="{visibility: !isEdit ? 'visible' : 'hidden'}"
        @click="edit(true)"
      ></gs-button>
      <div
        v-show="isEdit"
        class="action-btns"
      >
        <action-comfirm
          title=""
          @comfirm="updateTableInfo"
        >
          <gs-button
            slot="reference"
            type="primary"
          >
          </gs-button>
        </action-comfirm>
        <gs-button
          @click="edit(false)"
        ></gs-button>
      </div>
    </div>
    <gs-form
      ref="form"
      class="table-info-form"
      label-width="95px"
    >
      <h3>
        <i
          :style="{transform: packUp1 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp1=!packUp1"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp1">
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.showTableName }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span v-if="tableInfo.editTag">
              <gs-tag
                v-for="(tag, index) in tableInfo.tags"
                :key="tag.tagId"
                :title="tag.tagName"
                class="table-tag"
                closable
                @close="handleDelTag(index)"
              >{{ tag.tagName }}</gs-tag>
            </span>
            <span v-else>
              <gs-tag
                v-for="tag in tableInfo.tags"
                :key="tag.tagId"
                :title="tag.tagName"
                class="table-tag"
              >{{ tag.tagName }}</gs-tag>
            </span>
            <span v-if="!tableInfo.tags || !tableInfo.tags.length"></span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.owner }}</span>
          </gs-form-item>
          <gs-form-item
            label="Owner"
          >
            <gs-tag
              v-for="(tag, index) in tableInfo.dataOwner"
              :key="index"
              :title="tag.name"
              class="table-tag"
              closable
              @close="handleDelDataOwner(tag, index)"
            >{{ tag.name }}</gs-tag>
            <span v-if="!tableInfo.dataOwner || !tableInfo.dataOwner.length"></span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.updateTime || '-' }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <i
              class="iconfont icon-yuanshuju"
            />
            <span>{{ tableInfo.databaseName }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.type }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.partitionTable ? '' : '' }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.format }}</span>
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.location }}</span>
          </gs-form-item>
          <!-- <gs-form-item
            ref="description"
            label=""
          >
            <span v-if="!isEdit">{{ tableInfo.description || '-' }}</span>
            <gs-input
              v-else
              v-model="description"
              placeholder=""
            />
          </gs-form-item> -->
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp2 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp2=!packUp2"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp2">
          <gs-form-item
            ref="subordinateSystem"
            label=""
            prop="subordinateSystem"
          >
            <span v-if="!isEdit">{{ tableInfo.subordinateSystem || '-' }}</span>
            <gs-input
              v-else
              v-model="editForm.subordinateSystem"
              placeholder=""
            />
          </gs-form-item>
          <gs-form-item
            label=""
            prop="subordinateDatabase"
          >
            <span v-if="!isEdit">{{ tableInfo.subordinateDatabase || '-' }}</span>
            <gs-input
              v-else
              v-model="editForm.subordinateDatabase"
              placeholder=""
            />
          </gs-form-item>
          <gs-form-item
            label=""
            prop="systemAdmin"
          >
            <!-- <span>{{ tableInfo.systemAdmin && tableInfo.systemAdmin.length ? tableInfo.systemAdmin.join(',') : '-' }}</span> -->
            <span v-if="!isEdit">{{ tableInfo.systemAdmin || '-' }}</span>
            <gs-input
              v-else
              v-model="editForm.systemAdmin"
              placeholder=""
            />
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.createTime || '-' }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp3 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp3=!packUp3"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp3">
          <gs-form-item
            label=""
            prop="dataWarehouseAdmin"
          >
            <span v-if="!isEdit">{{ tableInfo.dataWarehouseAdmin || '-' }}</span>
            <gs-input
              v-else
              v-model="editForm.dataWarehouseAdmin"
              placeholder=""
            />
          </gs-form-item>
          <gs-form-item
            label=""
            prop="dataWarehouseDescription"
          >
            <span v-if="!isEdit">{{ tableInfo.dataWarehouseDescription || '-' }}</span>
            <gs-input
              v-else
              v-model="editForm.dataWarehouseDescription"
              placeholder=""
            />
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp4 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp4=!packUp4"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp4">
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.relations && tableInfo.relations.length ? tableInfo.relations.join(',') : '-' }}</span>
          </gs-form-item>
          <gs-form-item
            prop="catalogAdmin"
            label=""
          >
            <!-- <span>{{ tableInfo.catalogAdmin && tableInfo.catalogAdmin.length ? tableInfo.catalogAdmin.join(',') : '-' }}</span> -->
            <span v-if="!isEdit">{{ tableInfo.catalogAdmin || '-' }}</span>
            <gs-input
              v-else
              v-model="editForm.catalogAdmin"
              placeholder=""
            />
          </gs-form-item>
          <gs-form-item
            label=""
          >
            <span>{{ tableInfo.relationTime || '-' }}</span>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp5 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp5=!packUp5"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div
          v-show="!packUp5"
          class="business-dimension"
        >
          <div
            v-for="(item, index) in tableInfo.businessObjects"
            :key="index"
            class="business-item"
          >
            <div class="item-content">
              <span class="label-text"></span>
              <span class="value-text">{{ item.businessObject }}</span>
            </div>
            <div class="item-content">
              <span class="label-text"></span>
              <span class="value-text">{{ item.department }}</span>
            </div>
            <div>
              <span class="label-text"></span>
              <span class="value-text">{{ item.businessLeader }}</span>
            </div>
          </div>
          <span v-if="!tableInfo.businessObjects || !tableInfo.businessObjects.length"></span>
        </div>
      </gs-collapse-transition>
      <h3>
        <i
          :style="{transform: packUp6 ? 'rotate(-90deg)' : 'rotate(0deg)'}"
          class="gs-icon-down"
          @click="packUp6=!packUp6"
        />
        <span></span>
      </h3>
      <gs-collapse-transition>
        <div v-show="!packUp6">
          <gs-form-item
            label=""
            prop="database"
          >
            <gs-checkbox
              v-model="canRead"
              :disabled="true"
              class="table-permission"
            ></gs-checkbox>
            <gs-checkbox
              v-model="canWrite"
              :disabled="true"
              class="table-permission"
            ></gs-checkbox>
          </gs-form-item>
        </div>
      </gs-collapse-transition>
    </gs-form>
    <!-- <div
      v-show="isEdit"
      class="action-btns"
    >
      <action-comfirm
        title=""
        @comfirm="updateTableInfo"
      >
        <gs-button
          slot="reference"
          type="primary"
        >
        </gs-button>
      </action-comfirm>
      <gs-button
        @click="edit(false)"
      ></gs-button>
    </div> -->
  </div>
</template>

<script>
import './table-info.scss';
import * as tableInfoService from 'src/service/metadata-management/table-info';
import ActionComfirm from 'src/components/action-comfirm';
export default {
  components: {
    ActionComfirm
  },
  props: {
    tableInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isLoading: false,
      isEdit: false,
      packUp1: false,
      packUp2: false,
      packUp3: false,
      packUp4: false,
      packUp5: false,
      packUp6: false,
      description: '',
      editForm: {
        subordinateSystem: '',
        subordinateDatabase: '',
        systemAdmin: '',
        dataWarehouseAdmin: '',
        dataWarehouseDescription: '',
        catalogAdmin: ''
      },
      canWrite: false,
      canRead: false
    };
  },
  watch: {
    tableInfo: {
      handler: function (val) {
        this.canRead = val.tablePermission ? val.tablePermission.read : false;
        this.canWrite = val.tablePermission ? val.tablePermission.write : false;
      },
      immediate: true
    }
  },
  methods: {
    edit(flag) {
      this.isEdit = flag;
      if (flag) {
        const {
          subordinateSystem,
          subordinateDatabase,
          systemAdmin,
          dataWarehouseAdmin,
          dataWarehouseDescription,
          catalogAdmin
        } = this.tableInfo;
        this.editForm = {
          subordinateSystem,
          subordinateDatabase,
          systemAdmin,
          dataWarehouseAdmin,
          dataWarehouseDescription,
          catalogAdmin
        };
        this.$refs['subordinateSystem'].$el.scrollIntoView();
      }
    },
    updateTableInfo() {
      // if (this.description === this.tableInfo.description) {
      //   this.$Message.warning('');
      //   return;
      // }
      this.isLoading = true;
      tableInfoService.updateTableInfo({
        guid: this.$route.params.id,
        params: this.editForm
        // description: this.description
      }).then((res) => {
        this.isLoading = false;
        this.edit(false);
        // this.tableInfo.description = this.description;
        this.$emit('refreshTableInfo');
        this.$Message.success('');
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    handleDelTag(i) {
      const current = this.tableInfo.tags[i] || {};
      this.$Modal.confirm({
        title: '',
        content: `${current.tagName}`,
        onOk: () => {
          let tags = [];
          this.tableInfo.tags.map(item => {
            if (item.tagId !== current.tagId) {
              tags.push(item.tagId);
            }
          });
          this.isLoading = true;
          tableInfoService.addTableTag({
            table: {
              tableId: this.$route.params.id
            },
            tags
          }).then((res) => {
            this.isLoading = false;
            this.$Message.success('');
            this.$emit('refreshTableInfo');
          }).catch((res) => {
            this.isLoading = false;
          });
        }
      });
    },
    handleDelDataOwner(tag, index) {
      this.$Modal.confirm({
        title: '',
        content: `owner${tag.name}`,
        onOk: () => {
          this.isLoading = true;
          tableInfoService.deleteOwner({
            tableId: this.$route.params.id,
            pkId: [tag.pkId]
          }).then((res) => {
            this.isLoading = false;
            this.$Message.success('owner');
            this.$emit('refreshTableInfo');
          }).catch((res) => {
            this.isLoading = false;
          });
        }
      });
    }
  }
};
</script>




