import http from 'src/utils/http';
/**
 * 
 * @param {*} param
 */
export function addTemplate(param) {
  return http.post('quality/template', param);
}
/**
 * 
 * @param {*} templateId Id
 */
export function deleteTemplate(templateId) {
  return http.delete(`quality/template/${templateId}`);
}
/**
 * 
 * @param {*} templateId Id
 * @param {*} param
 */
export function updateTemplate(templateId, param) {
  return http.put(`quality/template/${templateId}`, param);
}
/**
 * 
 * @param {*} templateId Id
 */
export function getTemplateDetail(templateId) {
  return http.get(`quality/template/${templateId}`);
}
/**
 * 
 * @param {*} templateId Id
 */
export function closeTemplate(templateId, status) {
  return http.put(`quality/template/status/disable/${templateId}`);
}
/**
 * 
 * @param {*} templateId Id
 */
export function openTemplate(templateId, status) {
  return http.put(`quality/template/status/enable/${templateId}/${status}`);
}
/**
 * 
 * @param {*} tableId Id
 */
export function getTemplates(tableId) {
  return http.get(`quality/templates/${tableId}`);
}
/**
 * 
 * @param {*} templateId Id
 */
export function getNewestReport(templateId) {
  return http.get(`quality/newestReport/${templateId}`);
}
/**
 * 
 * @param {*} templateId Id
 */
export function getAllReports(tableId, templateId, offset, limit) {
  return http.post(`quality/reports/${tableId}/${templateId}`, {
    offset,
    limit
  });
}
/**
 * 
 * @param {*} templateId Id
 */
export function getReportDetail(templateId) {
  return http.get(`quality/report/${templateId}`);
}
/**
 * 
 */
export function getRules(tableId, buildType) {
  return http.get(`quality/rules/${tableId}/${buildType}`);
}
/**
 * 
 * @param {*} param
 */
export function getReportsDownloadUrl(param) {
  return http.post('quality/reports', param);
}
/**
 * 
 * @param {*}
 */
export function changeAlertStatus(reportId, status) {
  return http.put(`quality/report/alert/${reportId}/${status}`);
}
/**
 * 
 * @param {*}
 */
export function getTemplatePercent(templateId) {
  return http.get(`quality/template/${templateId}/percent`);
}



